import React, {useState} from 'react';
import {useAuth} from "../contexts/Auth";
import '../App.css'
import pepe from '../images/pepe_king.jpg'
import {Link} from 'react-router-dom'
import Introduce from './Introduce'
import AccountSetup from './AccountSetup'
function NavBar() {
    const {logout} = useAuth()
    const [AboutButtonState, setAboutButtonState] = useState(false)
    async function handleLogouts() {
        try {
            await logout()
        }
        catch {
            alert('Không thể đăng xuất')
        }
    }
    function handleAboutButton() {
        return setAboutButtonState(true)
    }
    function handleHomeButton() {
        return setAboutButtonState(false)
    }
        console.log(AboutButtonState)
        return (         
        <React.Fragment>
            <header>
                
                <Link to="/profile">
                    <div>
                        <img src={pepe} className="user-avt" alt="pepe" />
                    </div>
                </Link>
                <nav >
                    <ul className='nav_link'>
                        <li><a onClick={handleHomeButton}>Trang chủ</a></li>
                        <li><a onClick={handleAboutButton}>Giới thiệu</a></li>
                    </ul>
                </nav>  
                        <a className='signButton' onClick={handleLogouts} >Đăng xuất</a> 
            </header>
            <div>
                {AboutButtonState ? <Introduce/>: <AccountSetup />}
            </div>
        </React.Fragment> );
    }
export default NavBar;
